import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';

const AddPatient = ({ patients, setPatients }) => {
  const history = useHistory();
  const today = new Date().toISOString().slice(0, 16); //   

  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    specialty: '',
    appointment: today,
    medications: [],
    notes: '',
    username: '',
    password: '12345',
    status: 'pending',
  });

  const [credentials, setCredentials] = useState(null); //    

  useEffect(() => {
    setFormData((prev) => ({ ...prev, appointment: today }));
  }, [today]);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const addMedication = () => {
    const med = prompt('    (:  - 8 ):');
    if (med) {
      setFormData({ ...formData, medications: [...formData.medications, med] });
      toast.info(`  : ${med}`);
    }
  };

  const editMedication = (index) => {
    const newMed = prompt(' :', formData.medications[index]);
    if (newMed) {
      const updatedMeds = [...formData.medications];
      updatedMeds[index] = newMed;
      setFormData({ ...formData, medications: updatedMeds });
      toast.info('   ');
    }
  };

  const deleteMedication = (index) => {
    if (window.confirm('      ')) {
      const updatedMeds = formData.medications.filter((_, i) => i !== index);
      setFormData({ ...formData, medications: updatedMeds });
      toast.success('  ');
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const generatedUsername = `user_${Date.now()}`;
    const newPatient = {
      ...formData,
      id: Date.now().toString(),
      username: generatedUsername,
    };
    const updatedPatients = [...patients, newPatient];
    setPatients(updatedPatients);
    localStorage.setItem('patients', JSON.stringify(updatedPatients));
    
    //     
    setCredentials({ username: generatedUsername, password: formData.password });
    
    //    60 
    setTimeout(() => {
      setCredentials(null);
      console.log('      60 ');
    }, 60000);
    
    toast.success('   !');
    //     
    setTimeout(() => history.push('/patients'), 10000);
  };

  return (
    <div className="add-patient">
      <h2>  </h2>
      <form onSubmit={handleSubmit} className="add-patient-form">
        <div className="form-group">
          <label htmlFor="name"> </label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="  "
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="phone"> </label>
          <input
            type="tel"
            id="phone"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
            placeholder="  "
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="specialty"></label>
          <select
            id="specialty"
            name="specialty"
            value={formData.specialty}
            onChange={handleChange}
            required
          >
            <option value=""> </option>
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="appointment"> </label>
          <input
            type="datetime-local"
            id="appointment"
            name="appointment"
            value={formData.appointment}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group medications-group">
          <label></label>
          <button type="button" className="btn add-med-btn" onClick={addMedication}>
             
          </button>
          {formData.medications.length > 0 && (
            <ul className="medications-list">
              {formData.medications.map((med, index) => (
                <li key={index} className="medication-item">
                  <span>{med}</span>
                  <div className="med-actions">
                    <button
                      type="button"
                      className="btn edit-med-btn"
                      onClick={() => editMedication(index)}
                    >
                      
                    </button>
                    <button
                      type="button"
                      className="btn delete-med-btn"
                      onClick={() => deleteMedication(index)}
                    >
                      
                    </button>
                  </div>
                </li>
              ))}
            </ul>
          )}
        </div>

        <div className="form-group">
          <label htmlFor="notes"> </label>
          <textarea
            id="notes"
            name="notes"
            value={formData.notes}
            onChange={handleChange}
            placeholder="   ()"
          />
        </div>

        <button type="submit" className="btn primary-btn submit-btn">
           
        </button>
      </form>

      {/*     */}
      {credentials && (
        <div className="credentials-box">
          <h3>  </h3>
          <p> : <strong>{credentials.username}</strong></p>
          <p> : <strong>{credentials.password}</strong></p>
          <small>(    )</small>
        </div>
      )}
    </div>
  );
};

export default AddPatient;