import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

const PatientProfile = ({ patients, setPatients }) => {
  const { patientId } = useParams();
  const [patient, setPatient] = useState(null);
  const [medications, setMedications] = useState([]);
  const [description, setDescription] = useState('');
  const [nextVisit, setNextVisit] = useState('');
  const [reminder, setReminder] = useState('');

  useEffect(() => {
    console.log('patientId:', patientId); //    patientId
    console.log('patients:', patients); //    patients

    const selectedPatient = patients.find((p) => String(p.id) === patientId); //  p.id   
    if (selectedPatient) {
      setPatient(selectedPatient);
      setMedications(selectedPatient.medications || []);
      setDescription(selectedPatient.notes || '');
      setNextVisit(selectedPatient.appointment || '');
      setReminder(
        selectedPatient.medications?.length > 0
          ? `:  ${selectedPatient.medications[0]} `
          : '  '
      );
    } else {
      console.log('    ');
    }
  }, [patientId, patients]);

  const handleSave = () => {
    const updatedPatients = patients.map((p) =>
      String(p.id) === patientId
        ? { ...p, medications, notes: description, appointment: nextVisit }
        : p
    );
    setPatients(updatedPatients);
    localStorage.setItem('patients', JSON.stringify(updatedPatients));
    alert('   !');
  };

  const handleMedicationChange = (index, value) => {
    const updatedMedications = [...medications];
    updatedMedications[index] = value;
    setMedications(updatedMedications);
  };

  const addMedication = () => {
    setMedications([...medications, '']);
  };

  const removeMedication = (index) => {
    const updatedMedications = medications.filter((_, i) => i !== index);
    setMedications(updatedMedications);
  };

  if (!patient) {
    return (
      <div>
        {patients.length === 0 ? (
          <p>    </p>
        ) : (
          <p>    .      .</p>
        )}
      </div>
    );
  }

  return (
    <div className="patient-profile">
      <div className="card">
        <h2>{patient.name}</h2>
        <div className="patient-info">
          <div className="info-section">
            <h3></h3>
            <textarea
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="  ..."
            />
          </div>
          <div className="info-section">
            <h3>  </h3>
            <input
              type="datetime-local"
              value={nextVisit}
              onChange={(e) => setNextVisit(e.target.value)}
            />
          </div>
        </div>

        <div className="medications-section">
          <h3></h3>
          <ul className="medication-list">
            {medications.map((med, index) => (
              <li key={index} className="medication-item">
                <input
                  value={med}
                  onChange={(e) => handleMedicationChange(index, e.target.value)}
                  placeholder="  "
                />
                <div className="med-actions">
                  <button className="btn btn--accent" onClick={() => removeMedication(index)}>
                    
                  </button>
                </div>
              </li>
            ))}
          </ul>
          <button className="btn btn--success add-med-btn" onClick={addMedication}>
             
          </button>
        </div>

        <div className="reminder-section">
          <h3> </h3>
          <p>{reminder}</p>
        </div>

        <button className="btn save-btn" onClick={handleSave}>
           
        </button>
      </div>
    </div>
  );
};

export default PatientProfile;