import React, { useState } from 'react';

const Login = ({ setIsLoggedIn }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleLogin = (e) => {
    e.preventDefault();
    const patients = JSON.parse(localStorage.getItem('patients')) || [];
    const patient = patients.find(p => p.username === username && p.password === password);
  
    let userData = null;
  
    if (username === 'doctor' && password === '12345') {
      userData = { role: 'doctor', username: 'doctor' };
    } else if (patient) {
      userData = { role: 'patient', username: patient.name };
    }
  
    if (userData) {
      localStorage.setItem('loggedInUser', JSON.stringify(userData)); //    
      setIsLoggedIn(userData);
    } else {
      alert('      ');
    }
  };
  

  return (
    <div className="login">
      <h2> </h2>
      <form onSubmit={handleLogin}>
        <input
          type="text"
          placeholder=" "
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          required
        />
        <input
          type="password"
          placeholder=" "
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />
        <button type="submit" className="btn primary-btn"></button>
      </form>
    </div>
  );
};

export default Login;