import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Pie } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { toast } from 'react-toastify';
import appointmentIcon from '../assets/appointment-icon.png';
import medicineIcon from '../assets/medicine-icon.png';
import chatIcon from '../assets/chat-icon.png';
import reportIcon from '../assets/report-icon.png';
import hospitalImage from '../assets/hospital-image.jpg'; //  
import doctorImage1 from '../assets/doctor1.jpg'; //   1
import doctorImage2 from '../assets/doctor2.jpg'; //   2
import cleanlinessImage from '../assets/cleanliness-image.jpg'; //  

ChartJS.register(ArcElement, Tooltip, Legend);

const LandingPage = ({ patients, setPatients, messages, setMessages, notifications, setNotifications }) => {
  const safePatients = Array.isArray(patients) ? patients : [];
  const upcomingAppointments = safePatients.filter((p) => new Date(p.appointment) > new Date()).length;

  useEffect(() => {
    safePatients.forEach((p) => {
      if (p.appointment) {
        const timeDiff = new Date(p.appointment) - new Date();
        if (timeDiff > 0 && timeDiff < 24 * 60 * 60 * 1000) {
          setTimeout(() => {
            const notif = `  ${p.name}:    ${new Date(p.appointment).toLocaleString()}`;
            setNotifications((prev) => [...prev, { id: Date.now(), text: notif }]);
            toast.warn(notif);
          }, timeDiff - 23 * 60 * 60 * 1000);
        }
      }
    });
  }, [safePatients, setNotifications]);

  const sendReminder = (patient) => {
    const message = `  ${patient.name}:   ${new Date(patient.appointment).toLocaleString()}`;
    setNotifications([...notifications, { id: Date.now(), text: message }]);
    toast.info(message);
    console.log(`   ${patient.phone}: ${message}`);
  };

  const acceptBooking = (patientId) => {
    const updatedPatients = safePatients.map((p) =>
      p.id === patientId ? { ...p, bookingStatus: 'accepted' } : p
    );
    setPatients(updatedPatients);
    localStorage.setItem('patients', JSON.stringify(updatedPatients));
    toast.success('  ');
  };

  const rejectBooking = (patientId) => {
    const updatedPatients = safePatients.map((p) =>
      p.id === patientId ? { ...p, bookingStatus: 'rejected', appointment: null } : p
    );
    setPatients(updatedPatients);
    localStorage.setItem('patients', JSON.stringify(updatedPatients));
    toast.error('  ');
  };

  const suggestNewDate = (patientId) => {
    const newDate = prompt('   (: 2025-03-20T10:00):');
    if (newDate) {
      const updatedPatients = safePatients.map((p) =>
        p.id === patientId ? { ...p, appointment: new Date(newDate).toISOString(), bookingStatus: 'suggested' } : p
      );
      setPatients(updatedPatients);
      localStorage.setItem('patients', JSON.stringify(updatedPatients));
      toast.info('   ');
    }
  };

  const chatWithPatient = (patientName) => {
    const msg = prompt(`   ${patientName}:`);
    if (msg) {
      setMessages([...messages, { sender: 'doctor', patient: patientName, text: msg, time: new Date() }]);
      toast.info(`    ${patientName}`);
    }
  };

  const chartData = {
    labels: ['', ' ', ''],
    datasets: [
      {
        data: [safePatients.length, upcomingAppointments, notifications.length],
        backgroundColor: ['#00c4cc', '#00a8b0', '#ffcc00'],
      },
    ],
  };

  return (
    <div className="landing-page">
      {/* Hero Section */}
      <section className="hero">
        <div className="hero-content">
          <h1>Dr. Reminder -  </h1>
          <p>      </p>
          <div className="hero-buttons">
            <Link to="/add-patient" className="btn primary-btn"> </Link>
            <Link to="/patients" className="btn secondary-btn"> </Link>
          </div>
        </div>
        <img src={hospitalImage} alt=" " className="hero-image" />
      </section>

      {/*   */}
      <section className="about-us">
        <h2> </h2>
        <div className="about-content">
          <p>
                        .     .
          </p>
          <img src={hospitalImage} alt="" className="about-image" />
        </div>
      </section>

      {/*   */}
      <section className="info">
        <h2> </h2>
        <p>
              2020                  5000  .
        </p>
      </section>

      {/*   */}
      <section className="vision-mission">
        <h2> </h2>
        <div className="vision-mission-content">
          <div className="vision">
            <h3></h3>
            <p>             .</p>
          </div>
          <div className="mission">
            <h3></h3>
            <p>           .</p>
          </div>
        </div>
      </section>

      {/*  */}
      <section className="doctors">
        <h2></h2>
        <div className="doctors-grid">
          <div className="doctor-card">
            <img src={doctorImage1} alt=".  " className="doctor-image" />
            <h3>.  </h3>
            <p>   - 15   </p>
          </div>
          <div className="doctor-card">
            <img src={doctorImage2} alt=".  " className="doctor-image" />
            <h3>.  </h3>
            <p>  - 10   </p>
          </div>
        </div>
      </section>

      {/*    */}
      <section className="cleanliness">
        <h2>  </h2>
        <div className="cleanliness-content">
          <p>
                  .                .              .
          </p>
          <img src={cleanlinessImage} alt=" " className="cleanliness-image" />
          <div className="cleanliness-stats">
            <div className="stat-item">
              <h3>100%</h3>
              <p> </p>
            </div>
            <div className="stat-item">
              <h3>5 </h3>
              <p> </p>
            </div>
            <div className="stat-item">
              <h3>24/7</h3>
              <p> </p>
            </div>
          </div>
        </div>
      </section>

      {/*   */}
      <section className="bookings">
        <h2> </h2>
        {safePatients.filter((p) => p.appointment && !p.bookingStatus).length > 0 ? (
          <table className="table-container">
            <thead>
              <tr>
                <th></th>
                <th> </th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {safePatients.filter((p) => p.appointment && !p.bookingStatus).map((patient) => (
                <tr key={patient.id}>
                  <td>{patient.name}</td>
                  <td>{new Date(patient.appointment).toLocaleString()}</td>
                  <td>
                    <button className="btn accept-btn" onClick={() => acceptBooking(patient.id)}></button>
                    <button className="btn reject-btn" onClick={() => rejectBooking(patient.id)}></button>
                    <button className="btn suggest-btn" onClick={() => suggestNewDate(patient.id)}> </button>
                    <button className="btn chat-btn" onClick={() => chatWithPatient(patient.name)}></button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          <p>   </p>
        )}
      </section>

      {/*  */}
      <section className="notifications">
        <h2> </h2>
        {notifications.length > 0 ? (
          <ul>
            {notifications.map((notif) => (
              <li key={notif.id}>{notif.text}</li>
            ))}
          </ul>
        ) : (
          <p>   </p>
        )}
      </section>

      {/*  */}
      <section className="stats">
        <h2></h2>
        <div className="stats-grid">
          <div className="stat-item"><h3>{safePatients.length}</h3><p> </p></div>
          <div className="stat-item"><h3>{upcomingAppointments}</h3><p> </p></div>
          <div className="stat-item"><h3>{notifications.length}</h3><p> </p></div>
        </div>
        <div className="chart">
          <Pie data={chartData} />
        </div>
      </section>

      {/*   */}
      <section className="quick-actions">
        <h2> </h2>
        <div className="actions-grid">
          {safePatients.slice(0, 3).map((patient) => (
            <div key={patient.id} className="action-card">
              <p>{patient.name}</p>
              <button className="btn" onClick={() => sendReminder(patient)}> </button>
            </div>
          ))}
        </div>
      </section>
    </div>
  );
};

export default LandingPage;