import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../App.css'; //     CSS    

const Header = ({ isLoggedIn, setIsLoggedIn }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <header className="header">
      <div className="logo">
        <Link to="/">Dr. Reminder</Link>
      </div>
      <button className="hamburger" onClick={toggleMenu}>
        {isMenuOpen ? '' : ''} {/*     */}
      </button>
      <nav className={`nav ${isMenuOpen ? 'open' : ''}`}>
        {isLoggedIn.role ? (
          <>
            <Link to="/" onClick={() => setIsMenuOpen(false)}></Link>
            {isLoggedIn.role === 'doctor' && (
              <>
                <Link to="/patients" onClick={() => setIsMenuOpen(false)}></Link>
                <Link to="/add-patient" onClick={() => setIsMenuOpen(false)}> </Link>
                <Link to="/chat" onClick={() => setIsMenuOpen(false)}></Link>
                <Link to="/bookings" onClick={() => setIsMenuOpen(false)}></Link>
              </>
            )}
            <button
              className="btn logout-btn"
              onClick={() => {
                setIsLoggedIn({ role: null, username: null });
                setIsMenuOpen(false);
              }}
            >
               
            </button>
          </>
        ) : (
          <Link to="/" onClick={() => setIsMenuOpen(false)}> </Link>
        )}
      </nav>
    </header>
  );
};

export default Header;