import React from 'react';
import { useHistory } from 'react-router-dom';

const PatientList = ({ patients, setPatients }) => {
  const safePatients = Array.isArray(patients) ? patients : [];
  const history = useHistory();

  const sendReminder = (patient) => {
    const message = ` ${patient.name}    ${new Date(patient.appointment).toLocaleString()}.`;
    console.log(`   ${patient.phone}: ${message}`);
    alert(`     ${patient.name}`);
  };

  const deletePatient = (id) => {
    const updatedPatients = safePatients.filter((p) => p.id !== id);
    setPatients(updatedPatients);
    localStorage.setItem('patients', JSON.stringify(updatedPatients));
  };

  const editPatient = (patient) => {
    const updatedName = prompt('  :', patient.name);
    if (updatedName) {
      const updatedPatients = safePatients.map((p) =>
        p.id === patient.id ? { ...p, name: updatedName } : p
      );
      setPatients(updatedPatients);
      localStorage.setItem('patients', JSON.stringify(updatedPatients));
    }
  };

  const handlePatientClick = (patientId) => {
    history.push(`/patient/${patientId}`); //  useHistory   useNavigate
  };

  return (
    <div className="patient-list">
      <h2> </h2>
      {safePatients.length === 0 ? (
        <p>   </p>
      ) : (
        <table className="table-container">
          <thead>
            <tr>
              <th></th>
              <th> </th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {safePatients.map((patient) => (
              <tr
                key={patient.id}
                onClick={() => handlePatientClick(patient.id)}
                style={{ cursor: 'pointer' }}
              >
                <td>{patient.name || ' '}</td>
                <td>{patient.phone || ' '}</td>
                <td>{patient.specialty || ' '}</td>
                <td>{patient.appointment ? new Date(patient.appointment).toLocaleString() : ' '}</td>
                <td>{patient.medications?.join(', ') || '  '}</td>
                <td>{patient.notes || ' '}</td>
                <td onClick={(e) => e.stopPropagation()}>
                  <button className="btn" onClick={() => sendReminder(patient)}>
                     
                  </button>
                  <button className="btn edit-btn" onClick={() => editPatient(patient)}>
                    
                  </button>
                  <button className="btn delete-btn" onClick={() => deletePatient(patient.id)}>
                    
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default PatientList;